.App {
    --header_height: 60px;
    font-family: "Montserrat", serif;
    font-size: 22px;
    font-weight: 600;
    line-height: 26.82px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    max-width: 1280px;
    margin: 0 auto;
    min-height: 555px;
    background-color: #FFFFFF;
    padding-top: 101px;
    padding-bottom: 131px;
    padding-left: 70px;
    padding-right: 70px;
    margin-top: 130px;
    margin-bottom: 130px;
    position: relative;
    overflow: hidden;
}

.hw {
    border-bottom: 1px solid #D9D9D9;
    border-top: 1px solid #D9D9D9;
    padding-bottom: 24px;
}

.hwTitle {
    font-family: "Montserrat", serif;
    font-size: 22px;
    font-weight: 600;
    line-height: 26.82px;
    margin-bottom: 10px;
    margin-top: 30px;
}